import React, { useState, useEffect } from 'react';
import { catalogoService } from '../api/catalogoService';
import './FiltersBar.css';

const FiltersBar = ({ filters, onFilterChange, onSearch }) => {
  const [estados, setEstados] = useState([]);
  const [cargandoEstados, setCargandoEstados] = useState(true);

  // Cargar estados desde la API
  useEffect(() => {
    const cargarEstados = async () => {
      try {
        const estadosData = await catalogoService.getEstados();
        setEstados(estadosData);
      } catch (error) {
        console.error('Error al cargar estados:', error);
        // Fallback a estados estticos si la API falla
        setEstados([
          { id: 'buenos-aires', nombre: 'Buenos Aires' },
          { id: 'cordoba', nombre: 'Crdoba' },
          { id: 'santa-fe', nombre: 'Santa Fe' },
          { id: 'mendoza', nombre: 'Mendoza' },
          { id: 'tucuman', nombre: 'Tucumn' }
        ]);
      } finally {
        setCargandoEstados(false);
      }
    };

    cargarEstados();
  }, []);

  const niveles = [
    'Primario', 'Secundario', 'Terciario', 'Universitario'
  ];

  const tipos = [
    'Pblica', 'Privada', 'Cooperativa'
  ];

  const carreras = [
    'Medicina', 'Ingeniera', 'Derecho', 'Psicologa', 'Arquitectura',
    'Administracin', 'Contabilidad', 'Enfermera', 'Educacin',
    'Informtica', 'Tcnico Electromecnico', 'Tcnico en Programacin'
  ];

  return (
    <div className="filters-bar">
      <div className="filters-container">
        <div className="filters-grid">
          <div className="filter-group">
            <label htmlFor="provincia">Estado/Provincia</label>
            <select
              id="provincia"
              value={filters.provincia}
              onChange={(e) => onFilterChange('provincia', e.target.value)}
              disabled={cargandoEstados}
            >
              <option value="">
                {cargandoEstados ? 'Cargando estados...' : 'Todos los estados'}
              </option>
              {estados.map(estado => (
                <option key={estado.id} value={estado.nombre}>
                  {estado.nombre}
                </option>
              ))}
            </select>
          </div>

          <div className="filter-group">
            <label htmlFor="nivel">Nivel Educativo</label>
            <select
              id="nivel"
              value={filters.nivel}
              onChange={(e) => onFilterChange('nivel', e.target.value)}
            >
              <option value="">Todos los niveles</option>
              {niveles.map(nivel => (
                <option key={nivel} value={nivel}>{nivel}</option>
              ))}
            </select>
          </div>

          <div className="filter-group">
            <label htmlFor="tipo">Tipo de Institucin</label>
            <select
              id="tipo"
              value={filters.tipo}
              onChange={(e) => onFilterChange('tipo', e.target.value)}
            >
              <option value="">Todos los tipos</option>
              {tipos.map(tipo => (
                <option key={tipo} value={tipo}>{tipo}</option>
              ))}
            </select>
          </div>

          <div className="filter-group">
            <label htmlFor="carrera">Carrera</label>
            <select
              id="carrera"
              value={filters.carrera}
              onChange={(e) => onFilterChange('carrera', e.target.value)}
            >
              <option value="">Todas las carreras</option>
              {carreras.map(carrera => (
                <option key={carrera} value={carrera}>{carrera}</option>
              ))}
            </select>
          </div>
        </div>

        <div className="filters-bottom">
          <div className="checkbox-group">
            <input
              type="checkbox"
              id="becas"
              checked={filters.becas}
              onChange={(e) => onFilterChange('becas', e.target.checked)}
            />
            <label htmlFor="becas">Solo instituciones con becas disponibles</label>
          </div>

          <button className="search-btn" onClick={onSearch}>
             Buscar
          </button>
        </div>
      </div>
    </div>
  );
};

export default FiltersBar;
