// src/hooks/useCatalogo.js
import { useState, useEffect, useCallback } from 'react';
import { getCatalogo, getInstitucionById } from '../api/catalogoService';
import { useAuth } from '../lib/useAuth';

const useCatalogo = () => {
  const { idToken, isAuthenticated } = useAuth();
  const [catalogoState, setCatalogoState] = useState({
    data: [],
    loading: true,
    error: null,
    lastFetch: null
  });
  const [institucionesFiltradas, setInstitucionesFiltradas] = useState([]);

  // Funcin para cargar el catlogo
  const fetchCatalogo = useCallback(async (forceRefresh = false) => {
    // Si ya tenemos datos y no es refresh forzado, no hacer peticin
    if (!forceRefresh && catalogoState.data.length > 0 && !catalogoState.loading) {
      return;
    }

    setCatalogoState(prev => ({
      ...prev,
      loading: true,
      error: null
    }));

    try {
      const result = await getCatalogo(idToken);
      
      if (result.success) {
        const instituciones = result.data || [];
        setCatalogoState({
          data: instituciones,
          loading: false,
          error: null,
          lastFetch: new Date()
        });
        setInstitucionesFiltradas(instituciones);
      } else {
        setCatalogoState({
          data: [],
          loading: false,
          error: result.error,
          lastFetch: new Date()
        });
        setInstitucionesFiltradas([]);
      }
    } catch (error) {
      setCatalogoState({
        data: [],
        loading: false,
        error: {
          message: 'Error inesperado al cargar el catlogo',
          status: 500
        },
        lastFetch: new Date()
      });
      setInstitucionesFiltradas([]);
    }
  }, [idToken, catalogoState.data.length, catalogoState.loading]);

  // Funcin para obtener una institucin especfica
  const fetchInstitucionById = useCallback(async (institutionId) => {
    if (!institutionId) return null;

    try {
      const result = await getInstitucionById(institutionId, idToken);
      return result;
    } catch (error) {
      console.error('Error al obtener institucin:', error);
      return {
        success: false,
        data: null,
        error: { message: 'Error al cargar la institucin' }
      };
    }
  }, [idToken]);

  // Funcin para refrescar el catlogo
  const refreshCatalogo = useCallback(() => {
    fetchCatalogo(true);
  }, [fetchCatalogo]);

  // Funcin para filtrar instituciones
  const filtrarInstituciones = useCallback((filtros = {}) => {
    if (!catalogoState.data || catalogoState.data.length === 0) {
      setInstitucionesFiltradas([]);
      return;
    }

    let filtradas = [...catalogoState.data];

    // Filtro por provincia
    if (filtros.provincia) {
      filtradas = filtradas.filter(inst => 
        inst.provincia?.toLowerCase().includes(filtros.provincia.toLowerCase())
      );
    }

    // Filtro por nivel
    if (filtros.nivel) {
      filtradas = filtradas.filter(inst => 
        inst.nivel?.toLowerCase().includes(filtros.nivel.toLowerCase())
      );
    }

    // Filtro por tipo de institucin
    if (filtros.tipo) {
      filtradas = filtradas.filter(inst => 
        inst.tipo?.toLowerCase().includes(filtros.tipo.toLowerCase())
      );
    }

    // Filtro por carrera
    if (filtros.carrera) {
      filtradas = filtradas.filter(inst => 
        inst.carreras?.some(carrera => 
          carrera.toLowerCase().includes(filtros.carrera.toLowerCase())
        )
      );
    }

    // Filtro por becas
    if (filtros.becas) {
      filtradas = filtradas.filter(inst => inst.becas === true);
    }

    setInstitucionesFiltradas(filtradas);
  }, [catalogoState.data]);

  // Cargar catlogo al montar el componente o cuando cambie el token
  useEffect(() => {
    fetchCatalogo();
  }, [fetchCatalogo]);

  // Refrescar cuando el usuario se autentica
  useEffect(() => {
    if (isAuthenticated && idToken) {
      refreshCatalogo();
    }
  }, [isAuthenticated, idToken, refreshCatalogo]);

  return {
    // Estado
    instituciones: catalogoState.data,
    institucionesFiltradas,
    cargando: catalogoState.loading,
    error: catalogoState.error?.message || null,
    lastFetch: catalogoState.lastFetch,
    
    // Funciones
    refrescarCatalogo: refreshCatalogo,
    fetchInstitucionById,
    filtrarInstituciones,
    
    // Informacin adicional
    isEmpty: catalogoState.data.length === 0 && !catalogoState.loading,
    hasError: !!catalogoState.error
  };
};

// Exportacin por defecto
export default useCatalogo;
